#input {
    width: 1000px;
    margin: 50px auto;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #D2D2D2;
    text-align: center;
}

#input>input {
    width: 600px;
    height: 40px;
    font-size: 25px;
    padding-left: 20px;
    padding-right: 20px;
}

#input>#btn {
    height: 45px;
    width: 100px;
    background-color: #5A78FE;
    color: #FFF;
    margin: 0 15px;
    border: 0;
    border-radius: 2px;
    font-size: 25px;
    -webkit-box-shadow: none;
            box-shadow: none;
}

#input>#btn:hover {
    background-color: #222;
}


